
import wx from 'weixin-js-sdk'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapGetters,mapActions }from 'vuex'
import empty from '../components/empty'
export default {
name: "collageAll",
data(){
return{
useUp:true,
pageSize:10,
onFetching:false,
pullupDefaultConfig:{
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
}
},
computed:{
...mapGetters(['getptList'])
},
created(){



if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

this.getList(0,'',0)
},
methods:{
...mapActions(['myOrderGroupList']),
seeOrder(id){
wx.miniProgram.navigateTo({
url:'/pages/webview/index?orderId='+id+'&path=orderDetail'
})

},
goToPt(){
wx.miniProgram.redirectTo({
url:'/pages/groupShopReco/index'
})
},
gotoDetail(id){
wx.miniProgram.navigateTo({
url:'/pages/pinTdetail/index?id='+id +'&fr=l'
})
},
goToDe(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+id
})
},
getList(n,x,z){
this.showLoading()
this.myOrderGroupList({flag:z,data:{page:n,pageSize:this.pageSize,status:x},this:this,method:this.myOrderGroupList})
},
addList(arr){
this.$nextTick(() => {
this.$refs.scrollerBottom.donePullup()
this.$refs.scrollerBottom.reset()
})
},
onpulluploading(){
this.$refs.scrollerBottom.reset()
let _this = this;
let n =_this.getptList.all.lists.length
let max = _this.getptList.all.max
if(n < max){
_this.getList(Math.ceil(n/_this.pageSize),'',1)
}
else{
_this.$nextTick(() => {
_this.$refs.scrollerBottom.disablePullup()
})
}
}
},
filters:{
deld(s){
if(!s){
return s
}
let i = s.indexOf('.')
if(i == -1){
return i
}
else {
return s.slice(0,i)
}
}
},
components:{
LoadMore , Scroller ,empty
}
}



